// https://github.com/wojtekmaj/react-pdf/tree/master/sample

import React, { Component } from 'react';
import { Button} from 'react-bootstrap';
import 'react-pdf/dist/esm/Page/AnnotationLayer.css';
import PDFViewer from 'pdf-viewer-reactjs'

export default class PDFViewerV1 extends Component {
  state = {
    fileName: this.props.fileName, 
    visible: this.props.visible,
    file:  process.env.PUBLIC_URL + '/images/' + this.props.fileName,
    numPages: null,
  }

  handleClick() {
    this.setState({
      visible : !this.state.visible
    });
  }

  render() {
    const { file, numPages } = this.state;

    return (
      <div className="Example">
        <header>
        </header>
        <div className="Example__container">      
          <div className="Example__container__document">
            <button onClick={() => this.handleClick()}>
                {this.state.visible ? 'Sluiten' : this.state.fileName  }
            </button>
            {this.state.visible? 
             < PDFViewer
                  document= {{url:file}}
                  navbarOnTop= {{Type:true}}
                  hideRotation= {{Type:true}}
            />
             :""}
                <a href={file} target="_blank" rel="noopener noreferrer" download>
                  <Button>
                      <i className="fas fa-download"/>
                      Download
                  </Button>
                </a>
          </div>
        </div>
      </div>
    );
  }
}

